import './App.css';
import { connect } from 'react-redux';
import injectTapEventPlugin from 'react-tap-event-plugin';

import Nuages from './components/container/nuages';
import { setArticlesTotalCount, setCategoryId, setPage, setSearchWord, showArticles, showCategories } from './actions';

import request from 'superagent';


/**
 * api の URL
 * @type {string}
 */
var apiUrl = 'http://localhost:8080/';

/**
 * 基底となるパス
 * @type {string}
 */
var basePath = '/';

if (process.env.NODE_ENV === 'production') {
  //apiUrl = 'https://www.nuages.org/';
  apiUrl = 'http://150.95.178.37/';
  // basePath = '/~takao/build/';
  basePath = '/blog/';
}

export { apiUrl };
export { basePath };


injectTapEventPlugin();


function mapStateToProps(state) {
  return {
    articles: state.showArticlesReducer.articles,
    articlesTotalCount: state.showArticlesReducer.articlesTotalCount,
    categories: state.showCategoriesReducer.categories,
    page: state.showArticlesReducer.page,
    searchWord: state.showArticlesReducer.searchWord,
    selectedCategoryId: state.showArticlesReducer.selectedCategoryId,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    /**
     * 記事情報を取得
     * articleId !== '' の場合、api/articles/:articleId/ から記事情報を取得するとオブジェクトの構造が変わってしまうため、アクセスする API は以下で統一する
     * @param {string} [articleId='']  各記事（blog_article テーブル）の ID
     * @param {string} [categoryId=''] 各カテゴリ（blog_category テーブル）の ID
     * @param {string} [searchWord=''] 検索ボックスに入力されている検索ワード
     * @param {string} [page='1']      ページ番号
     */
    loadArticlesFromServer(articleId = '', categoryId = '', searchWord = '', page = '1') {
      request
        .get(apiUrl + 'api/blog/articles/')
        .set('Accept', 'application/json')
        .query({article_id: articleId})
        .query({category_id: categoryId})
        .query({search_word: searchWord})
        .query({page: page})
        .end(function(err, res) {
          if (res.ok) {
            dispatch(showArticles(res.body.results));
            dispatch(setArticlesTotalCount(res.body.count));
            dispatch(setCategoryId(categoryId));
            dispatch(setSearchWord(searchWord));
          } else {
            console.error('error');
          }
          console.log('complete');
        });
    },

    /**
     * カテゴリ情報を取得
     */
    loadCategoriesFromServer() {
      request
        .get(apiUrl + 'api/blog/categories/')
        .set('Accept', 'application/json')
        .end(function(err, res) {
          if (res.ok) {
            dispatch(showCategories(res.body.results));
          } else {
            console.error('error');
          }
          console.log('complete');
        });
    },

    /**
     * ページ番号管理用の state（state.showArticlesReducer.page）を更新
     * @param {number} page 更新するページ番号
     */
    setPage(page) {
      dispatch(setPage(page));
    },
  };
}

const App = connect(
  mapStateToProps,
  mapDispatchToProps
)(Nuages);

export default App;



